.navigation {
    width: 100vw;
    background-color: #2196F3;
    color: #ffffff;
    display: block;
    -webkit-user-select: none;
            user-select: none;
}

.navigation a {
    color: #ffffff;
    cursor: pointer;
    padding: 1em 2em;
    border: none;
    margin: 0;
    display: inline-block;
    text-decoration: none;
    background-color: transparent;
}

.navigation a:hover {
    background-color: #1565C0;
}

.dropdownContent {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.dropdownContent a {
    color: #424242;
    padding: 1em 2em;
    display: block;
}

.dropdownContent a:hover {
    background-color: #1565C0;
    color: #ffffff;
}

.dropdown {
    position: relative;
    display: inline-block;
}

.show {
    display: block;
}

/* Mobiles / Smaller Screens */

@media only screen and (max-width: 720px) {
    .navigation a {
        display: block;
    }

    .dropdown {
        display: block;
    }

    .dropdownContent {
        position: relative;
    }

    .dropdownContent a {
        color: #ffffff;
        padding-left: 4em;
    }

    .show {
        display: initial
    }
}

.card {
    width: 80vw;
    text-align: center;
    margin: 0 auto;
    padding: .5em;
    box-shadow: 0 2px 10px 1px #BDBDBD;
    border-radius: 4px;
    margin-bottom: 1em;
}

.card button {
    background-color: transparent;
    border: none;
    margin: 1em;
    outline: none;
}

.card button a {
    border: none;
    background-color: #4CAF50;
    color: #ffffff;
    padding: 1em 4em;
    outline: none;
    border-radius: 4px;
    text-decoration: none;
}

.card button a:hover {
    background-color: #2E7D32;
    cursor: pointer;
}
footer {
    width: 100vw;
    margin: 0;
    padding: 1em 0;
    background-color: #37474F;
    color: #ffffff;
    text-align: center;
}

footer nav a {
    color: #ffffff;
    text-decoration: none;
    padding: 0.5em;
}

footer nav a:hover {
    text-decoration: underline;
}
.parallax {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
html, body {
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, Sans-serif;
    color: #424242;
    background-color: #FAFAFA;
    width: 100vw;
    height: 100vh;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}


/* Pemament fix for overflowing elements */
#root {
    overflow-x: hidden; 
}

/* Main Styled Elements */

code {
    background-color: #ECEFF1;
    padding: 1em;
    margin: 1em;
    display: block;
    border-radius: 4px;
    word-wrap: break-word;
}

code .tag {
    color: #9E9E9E;
}

code .element {
    color: #2196F3;
}

code .attribute {
    color: #f44336;
}

/* Common styles */

main h1 {
    text-align: center;
}

main p {
    text-align: center;
}
